<template>
	<div>
		<header>
			<router-link to='/' class="goback"></router-link>
			<select class="s_class" v-model='value'>
				<option v-for='c in c_list' :value='c.name'>{{c.name}}</option>
			</select>
		</header>
		<div class="info_tit">
			<p>排名</p>
			<p>姓名</p>
			<p>积分</p>
			<p>阅读量(万)</p>
		</div>
		<div class="info_l_s" v-for="(l, i) in list">
			<div>
				<p class="o_nbr">{{i+1}}</p>
			</div>
			<div>
				<img class="s_tx" :src="l.avatar">
				<p class="s_name">{{l.stuname}}</p>
				<p class="designation">{{l.level}}</p>
			</div>
			<div>
				<p class="s_rank">{{l.score}}</p>
			</div>
			<div>
				<p class="s_grade">{{l.words}}</p>
			</div>
		</div>
		<tips :msg='message' v-show='flag'></tips>
	</div>
</template>
<script>
	var timer=null;
	import tips from './msgtips.vue'
	export default {
		data(){
			return {
				// 选中id
				c_id: 0,
				// 班级排名列表
				list: [],

				//班级列表
				c_list: [],

				value: '',

				// 页数
				page: 1,

				// 提示框信息
				message: '',
				flag: false,

				// 上拉加载
				sl_flag: false

			}
		},
		created: function(){
			var self=this;
			// 显示加载中
			this.message='加载中';
			this.flag=true;
			// --------------请求班级列表begin--------------
			// 先判断全局变量中是否有数据，若无则向后台请求数据
			if(!this.util.getData('clist')){
				$.ajax({
					url: self.globalurl.host+self.globalurl.url.bjlb,
					data: {"loginAccount": self.util.getSessionData("tId"), "token": 124567898},
					dataType: 'json',
					type: 'POST',
					success: function(data){
						if(data.statusCode==200&&data.classes.length!=0){
							// for(let i=0;i<data.classes.length;i++){
							// 	self.c_list.push(data.classes[i]);
							// }
							self.c_list=data.classes;
							// alert(self.c_list[0].name);
							self.util.setData('cid', self.c_list[0].id);
							self.util.setData('clist', self.c_list);
							self.value=self.$route.query.name;
						}
					}
				})
			}else{
				this.c_list=this.util.getData('clist');
				this.value=this.$route.query.name;
			}
			// --------------请求班级列表end--------------

			// 上拉加载
			window.addEventListener('scroll', function(){
				if(document.body.scrollTop+document.documentElement.clientHeight==document.body.scrollHeight){
						// 只在查看班级页面有效
						if(self.$route.path=='/ckbj'){
							self.page+=1;
							self.message='加载中';
							self.flag=true;
							self.sl_flag=true;
							self._query(self.c_id, self.page);
						}
				}
			})
		},
		methods: {
			_query: function(id, page){
				var self=this;
				if(!this.sl_flag){
					this.list=[];
				}
				// --------------请求班级排名begin--------------
				$.ajax({
					url: self.globalurl.host+self.globalurl.url.pmlb,
					data: {"classid":id,"page": page,"pageSize": 10, "token":"1222"},
					dataType: "json",
					type: 'POST',
					success: function(data){
						if(data.statusCode==200&&data.infos.length!=0){
							for(let i=0;i<data.infos.length;i++){
								self.list.push(data.infos[i]);
								self.flag=false;
								self.sl_flag=false;
							}
						}
						if(data.statusCode==404){
							clearTimeout(timer);
							timer=null;
							self.message='暂无数据';
							timer=setTimeout(function(){
								self.flag=false;
							}, 2000);
							self.sl_flag=false;
						}
						if(data.statusCode==500){
							clearTimeout(timer);
							timer=null;
							self.message='服务器开小差啦，请稍后再试';
							timer=setTimeout(function(){
								self.flag=false;
							}, 2000);
							self.sl_flag=false;
						}
					}
				})
				// --------------请求班级排名end--------------
			}
		},
		watch: {
			value: function(){
				for(let i=0;i<this.c_list.length;i++){
					if(this.c_list[i].name==this.value){
						this.page=1;
						this.c_id=this.c_list[i].id;
						this._query(this.c_id, this.page);
						break;
					}
				}
			}
		},
		components: {
			"tips": tips
		}
	}
</script>
<style scoped>
	.s_class{
		padding: 0px 0.36rem;
		font-size: 0.38rem;
		color: #fff;
		width: 35%;
		height: 100%;
		border: 0px;
		background-color: rgba(255,255,255,0);
		background-image: url('../../static/images/ico_06.png');
		background-position: center right;
		background-repeat: no-repeat;
		background-size: 0.22rem 0.11rem;
	}
	.s_class option{
		color: black;
	}
	.info_tit{
		margin-top: 0.88rem;
		display: flex;
		flex-flow: row;
		width: 100%;
		height: 0.7rem;
		background-color: #fff;
	}
	.info_tit p{
		text-align: center;
		line-height: 0.7rem;
		font-size: 0.30rem;
		color: #808080;
		border-bottom: 0.01rem solid #e7e7e7;
	}
	.info_tit p:nth-child(1){
		flex: 1;
	}
	.info_tit p:nth-child(2){
		flex: 3;
	}
	.info_tit p:nth-child(3){
		flex: 2;
	}
	.info_tit p:nth-child(4){
		flex: 2;
	}
	.info_l_s{
		display: flex;
		flex-flow: row;
		width: 100%;
		height: 1.2rem;
		background-color: #fff;
	}
	.info_l_s:nth-of-type(1){
		margin-top: 1.6rem;
	}
	.info_l_s div{
		padding: 0.2rem 0px;
	}
	.info_l_s div:nth-child(1){
		position: relative;
		flex: 1;
	}
	.info_l_s div:nth-child(2){
		flex: 3;
	}
	.info_l_s div:nth-child(3){
		flex: 2;
	}
	.info_l_s div:nth-child(4){
		flex: 2;
	}
	.o_nbr{
		position: absolute;
		top: 50%;
		left: 50%;
		margin-top: -0.26rem;
		margin-left: -0.215rem;
		line-height: 0.52rem;
		text-align: center;
		width: 0.43rem;
		height: 0.52rem;
		font-size: 0.36rem;
		color: #808080;
		background-size: 100% 100%;
	}
	.info_l_s:nth-of-type(2) .o_nbr{
		text-indent: -99999999px;
		background-image: url('../../static/images/pic_gold.png');
	}
	.info_l_s:nth-of-type(3) .o_nbr{
		text-indent: -99999999px;
		background-image: url('../../static/images/pic_silver.png');
	}
	.info_l_s:nth-of-type(4) .o_nbr{
		text-indent: -99999999px;
		background-image: url('../../static/images/pic_copper.png');
	}
	.s_tx{
		float: left;
		margin-left: 0.1rem;
		margin-right: 0.2rem;
		width: 0.8rem;
		height: 0.8rem;
		border-radius: 100%;
	}
	.s_name{
		margin-top: 0.07rem;
		font-size: 0.35rem;
		color: #1a1a1a;
	}
	.designation{
		margin-top: 0.11rem;
		font-size: 0.28rem;
		color: #808080;
	}
	.info_l_s:nth-of-type(2) .designation,
	.info_l_s:nth-of-type(3) .designation,
	.info_l_s:nth-of-type(4) .designation{
		color: #ffb700;
	}
	.s_rank,.s_grade{
		font-size: 0.4rem;
		color: #40cc8b;
		width: 100%;
		height: 100%;
		text-align: center;
		line-height: 0.8rem;
	}
	.s_grade{
		color: #808080;
	}
	.tips{
		text-align: center;
		font-size: 0.33rem;
		color: #666666;
		margin: 0.2rem 0px;
	}
</style>


