                           <template>
  <div id="app">
    <router-view></router-view>
  </div>
</template>

<script>
  export default {
    data(){
      return {
      }
    },
    created: function(){
      var self=this;
      $.ajax({
        url: self.globalurl.host+self.globalurl.url.tId,
        data: {},
        dataType: 'json',
        // type: 'GET',
        type: 'POST',
        async: false,
        success: function(data){
          if(data.StatusCode==200){
            self.util.setSessionData("tId", data.data.userName);
            // self.util.setSessionData("tId", data.data.userId);
          }
        }
      })
      var paramUrl = window.location.href.split("#/")[0];
      $.ajax({
        type : "POST",
        dataType : "html",
        data : {paramUrl: paramUrl},
        async:false,
        url : self.globalurl.host+self.globalurl.url.jssdk,
        success : function (a) {
          self.util.setSessionData('rets',  eval("(" + a + ")"));
        }
      })
    }
  }
</script>

<style>
  html,body{
    background: #f3f3ef;
  }
  /*头部*/
  header{
    position: fixed;
    z-index: 999;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 0.88rem;
    background-color: #40cc8b;
    text-align: center;
  }
  header p{
    font-size: 0.40rem;
    line-height: 0.88rem;
    color: #fff;
  }
  .goback{
    position: absolute;
    top: 50%;
    left: 0.25rem;
    margin-top: -0.185rem;
    display: inline-block;
    width: 0.37rem;
    height: 0.37rem;
    background-image: url('../static/images/pic_fh.png');
    background-size: 100% 100%;
  }
</style>



