<template>
	<div id="sc" style=" background-color: #fff; ">
		<header>
			<router-link :to='"/sk/sk_search?path="+tj_yl' ><img class="goback" src="../../static/images/pic_ss.png"></router-link>
			<p>书库</p>
			<img class="goback_r" src="../../static/images/ico_18.png">
		</header>
		<div class="nav">
			<ul  class="nav_li" >
				<li v-for='(item,index) in data' @click='_light(index, item.val)'><span v-bind:class="{'navYes':flag==index}">{{item.name}}</span></li>
			</ul>
		</div>
		<div>
			<ul class="nav1">
				<li style="border-right: #e6e6e6 solid 0.01rem;background-color: #fff;">
					<select class="s_class" v-model='nj_c'>
						<option selected value=''>全部</option>
						<option v-for='nj in nj_list' :key='nj.id' :value='nj.name'>{{nj.name}}</option>
					</select>
				</li>
				<li style="background-color: #fff;">
					<select class="s_class" v-model='lb_c'>
						<option selected value=''>全部</option>
						<option v-for='lb in lb_list' :key='lb.id' :value='lb.id'>{{lb.name}}</option>
					</select>
				</li>
			</ul>
		</div>
		<foot :show="1"></foot>
		<div class="qx" v-show='qx_flag'>
			<a href='javascript:void(0)' @click='_goto'>取&nbsp;&nbsp;消</a>
		</div>
		<tips :msg='message' v-show='t_flag'></tips>
		<div class="ydjy_list" v-for="sc in sc_list" :key='sc.id'>
			<router-link :to="'/sc/xq?id='+sc.id+'&type='+type" >
			<div class="ydjy_l">
				<img :src="sc.pic" class="ydjy_lPic" style=" position: relative; ">
			</div>
	 		</router-link>
			<div class="ydjy_r">
				<router-link :to="'/sc/xq?id='+sc.id+'&type='+type">
				<div class="hdzq_titil" >{{sc.name}}</div>
				<div class="author">{{sc.author}}<span>&nbsp;&nbsp;&nbsp;著</span></div>
				<div style="display: flex; justify-content: left; align-items: center;">
					<span class="ts_label">故事</span>
					<span class="ts_label">科普</span>
					<span class="ts_label">美国</span>
				</div>
				<ul class="record">
					<li><img src="../../static/images/ico_see.png" class="pic_see" ></img>&nbsp;{{sc.view}}</li>
					<li @click='_dz'><img :src="dzImg" class="pic_praise"></img >&nbsp;{{sc.love}}</li>
				</ul></router-link>
				<div class="btn">
					<a href="javascript:void(0)"></a>
					<a href="javascript:void(0)" @click='_addTs(sc)'></a>
					<a href="javascript:void(0)" v-if='sc.haveQuestion==1'></a>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
	var sId=null;
	var timer=null;
	import tips from './msgtips.vue'
	import f from './footer.vue'
	export default {
		data(){
			return {
				// 提示框
				message: '',
				t_flag: false,
				// 底部取消按钮
				qx_flag: false,
				// 年级列表
				nj_list: [],
				// 选中年级
				nj_c: '',
				// 类别列表
				lb_list: [],
				// 选中类别
				lb_c: '',
				// 阅读教研 列表
				sc_list: [],
				data:[
					{val:1,name:'书城'},
					{val:1,name:'专家推荐'},
					{val:2,name:'老师推荐'}
				],
				// 类别
				type: 1,
				flag:0,
				// 是否从添加图书(true)来还是预览(false)
				tj_yl: true,
				// 页码
				page: 1,
				// 上拉加载
				sl_flag: false,
				// 学校id
				schoolId: '',
				// 点赞
				dzImg: '../../static/images/ico_heart.png'
			}
		},
		name: 'sc',
		methods: {
			_dz: function(){
				this.dzImg='../../static/images/ico_heart_show.png'
			},
			_light:function(i, type){
		            	this.flag=i;
		            	this.type=type;
		            	// 加载数据
		            	this._load();
			},
			_addTs: function(ts){
				var self=this;
				if(ts.haveQuestion==1){
					// 当前有数据保存在DOM存储里(从发布任务页面跳过来)
					if(this.util.getSessionData('rwinfo')){
						var rwInfo=this.util.getSessionData('rwinfo');
						if(rwInfo.b_list&&rwInfo.bstr){
							// 重复则什么都不做
							for(let i=0;i<rwInfo.b_list.length;i++){
								if(rwInfo.b_list[i].id==ts.id){
									clearTimeout(timer);
									timer=null;
									this.message='已添加';
									this.t_flag=true;
									timer=setTimeout(function(){
										self.t_flag=false;
									}, 2000);
									return false;
								}
							}
							rwInfo.b_list.push(ts);
							rwInfo.bstr.push(ts.id+"+"+self.type);
						}else{
							rwInfo.b_list=[];
							rwInfo.bstr=[];
							rwInfo.b_list.push(ts);
							rwInfo.bstr.push(ts.id+"+"+self.type);
						}
						this.util.setSessionData('rwinfo', rwInfo);
						this._goto();
					}else{
						// 当前没有数据保存在DOM存储里
						var rwInfo={};
						rwInfo.b_list=[];
						rwInfo.bstr=[];
						rwInfo.b_list.push(ts);
						this.util.setSessionData('rwinfo', rwInfo);
						rwInfo.bstr.push(ts.id+"+"+self.type);
						this.$router.push("/fbrw");
					}
				}else{
					clearTimeout(timer);
					timer=null;
					this.message='该书暂无题库';
					this.t_flag=true;
					timer=setTimeout(function(){
						self.t_flag=false;
					}, 2000);
					return false;
				}
			},
			_goto: function(){
				// 判断页面是从添加图书跳转还是预览跳转
				if(this.tj_yl){
					this.$router.push("/fbrw/ts");
				}else{
					this.$router.push("/fbrw/yl");
				}
			},
			_load: function(){
				var self=this;
				if(!this.sl_flag){
					this.sc_list=[];
					this.page=1;
				}
				if(this.type==2){
					this.schoolId=sId;
				}else{
					this.schoolId='';
				}
				this.message='加载中';
				this.t_flag=true;
				// --------------请求图书列表begin--------------
				$.ajax({
					url: self.globalurl.host+self.globalurl.url.tslb,
					data: {"moduleId": self.lb_c, "grade": self.grade, "type": self.type, "schoolId": self.schoolId, "page": self.page, "pageSize": 10},
					dataType: 'json',
					type: 'POST',
					success: function(data){
						if(data.statusCode==200&&data.books.length!=0){
							for(let i=0;i<data.books.length;i++){
								self.sc_list.push(data.books[i]);
							}
							self.sl_flag=false;
							self.t_flag=false;
						}
						if(data.statusCode==404){
							clearTimeout(timer);
							timer=null;
							self.message='暂无数据';
							timer=setTimeout(function(){
								self.t_flag=false;
							}, 2000);
							self.sl_flag=false;
						}
						if(data.statusCode==500){
							clearTimeout(timer);
							timer=null;
							self.message='服务器开小差啦，请稍后再试';
							self.t_flag=true;
							timer=setTimeout(function(){
								self.t_flag=false;
							}, 2000);
							self.sl_flag=false;
						}
					}
				})
				// --------------请求图书列表end--------------
			}
		},
		components: {
			'foot': f, // 添加底部导航组件,
			'tips': tips
		},
		created: function(){
			// 判断是不是从发布任务来
			if(this.util.getSessionData('tj_yl')){
				this.qx_flag=true;
				if(this.util.getSessionData('tj_yl')==1){
					// 添加图书
					this.tj_yl=true;
				}else{
					// 预览
					this.tj_yl=false;
				}
			}
			var self=this;
			// --------------请求年级列表begin--------------
			$.ajax({
				url: self.globalurl.host+self.globalurl.url.sknj,
				dataType: 'json',
				type: 'POST',
				success: function(data){
					if(data.statusCode==200&&data.grade.length!=0){
						self.nj_list=data.grade;
					}
				}
			})
			// --------------请求年级列表end--------------

			// --------------请求类别列表begin--------------
			$.ajax({
				url: self.globalurl.host+self.globalurl.url.sklb,
				dataType: 'json',
				type: 'POST',
				success: function(data){
					if(data.statusCode==200&&data.modules.length!=0){
						self.lb_list=data.modules;
					}
				}
			})
			// --------------请求年级列表end--------------

			// --------------请求学校idbegin--------------
			$.ajax({
				url: self.globalurl.host+self.globalurl.url.grzx,
				data: {'loginAccount': self.util.getSessionData("tId")},
				dataType: 'json',
				type: 'POST',
				success: function(data){
					if(data.statusCode==200){
						sId=data.schoolId;
					}
				}
			})
			// --------------请求学校id列表end--------------

			// 加载图书
			this._load();

			// 上拉加载
			window.addEventListener('scroll', function(){
				if(document.body.scrollTop+document.documentElement.clientHeight==document.body.scrollHeight){
					if(self.$route.path=='/sc'){
						self.page+=1;
						self.sl_flag=true;
						self._load();
					}
				}
			})
		},
		watch: {
			nj_c: function(){
				this._load();
			},
			lb_c: function(){
				this._load();
			}
		},
		beforeRouteEnter(to, from, next){
			if(from.path=='/fbrw/ts'){
				// 如果从发布任务跳转过来 则显示底部取消按钮
				next(function(vm){
					vm.qx_flag=true;
					vm.tj_yl=true;
					vm.util.setSessionData('tj_yl', 1);
				});
			}else if(from.path=='/fbrw/yl'){
				// 如果从发布任务跳转过来 则显示底部取消按钮
				next(function(vm){
					vm.qx_flag=true;
					vm.tj_yl=false;
					vm.util.setSessionData('tj_yl', 2);
				});
			}else{
				next();
			}
		}
	}
</script>
<style scoped>
	/*头部*/
	header p{
		font-size: 0.36rem;
		line-height: 0.88rem;
		color: #fff;
	}
	.goback{
		position: absolute;
		top: 50%;
		left: 0.25rem;
		margin-top: -0.185rem;
		display: inline-block;
		width: 0.37rem;
		height: 0.37rem;
		background: no-repeat;
	}
	.goback_r{
		position: absolute;
		top: 50%;
		right: 0.25rem;
		margin-top: -0.185rem;
		display: inline-block;
		width: 0.37rem;
		height: 0.37rem;
	}
	.nav{
		width: 100%; 
		height: 0.8rem; 
		position: relative; 
		top:0.88rem; 
		/*background-color: #6fa1e8; */
		border-bottom: #e6e6e6 0.01rem solid;
		line-height: 0.8rem; 
		display: inline-block;
	}
	.navYes{
		 background-color: #40cc8b; 
		 color: #fff;
		 padding: 0.1rem 0.25rem; 
		 border-radius: 0.08rem;
	}
	.nav_li li{
		 float: left; 
		 width: 33.333%; 
		 text-align: center; 
		 font-size: 0.32rem; 
		 color: #666;
	}
	.nav1{
		width: 100%; 
		height: 0.86rem;	
		position: relative; 
		top:0.85rem;  
		background-color: rgba(255,255,255,0);
		background-image: url('../../static/images/bj_line.png');
		background-position: bottom;
		background-repeat: repeat-x;		
	}
	.nav1 li{
		width: 50%; 
		float: left; 
	}
	.b_title{
		font-size: 0.35rem;
		margin-top: 0.25rem;
		margin-bottom: 0.1rem;
		color: #333333;
	}
	.s_class{
		padding: 0px 0.36rem;
		font-size: 0.32rem;
		color: #808080;
		width: 92%;
		height: 0.8rem;
		line-height: 0.8rem;
		border: 0px;
		background-color: rgb(255,255,255);
		background-image: url('../../static/images/ico_17.png');
		background-position: center right;
		background-repeat: no-repeat;
		background-size: 0.35rem 0.18rem;
	}
	.s_class option{
		color: black;
	}

	.ydjy_list{
		width: 100%;  
		background-color: #fff; 
		position: relative;
		top:0.85rem; 
		border-bottom: #ececec 0.01rem solid;
		overflow: hidden;
		padding: 0.2rem 0px;
	}
	.ydjy_list:last-child{
		margin-bottom: 1.9rem;
	}
	.ydjy_l{
		width: 1.9rem; 
		height:2.4rem; 
		margin: 0.25rem 0.4rem 0.18rem 0.32rem; 
		float: left; 
	}
	.ydjy_lPic{
		width: 1.9rem;
		height: 2.4rem; 
	}
	.ydjy_r{
		height: 100%; 
		width: 4.55rem; 
		float: left; 
	}
	.ico_mark{
		background-color: #edc900; 
		border-radius: 0.8rem; 
		vertical-align: middle; 
		padding: 0.04rem 0.15rem; 
		color: #fff; 
		font-size: 0.3rem; 
	}
	.play{
		position: absolute; 
		background-color: #000;  
		width: 1.91rem; 
		height:2.4rem;   
		top:0.25rem; 
		left: 0.31rem; 
		z-index: 88; 
		display: flex; 
		align-items: center; 
		justify-content: center; 
		filter:alpha(opacity=30);  /*支持 IE 浏览器*/
		-moz-opacity:0.30; /*支持 FireFox 浏览器*/
		opacity:0.30;  /*支持 Chrome, Opera, Safari 等浏览器*/	
	}
	.markName{
		width: 0.23rem; 
		height: 0.26rem; 
		padding-top: 0.03rem;
	}
	.record ,.record1{
		width: 100%; 
		height: 0.5rem;
		font-size: 0.3rem; 
		display: inline-block; 
		color: #999; 
		font-family: 微软雅黑; 
	}
	.record1 li{
		float: left;
		width: 30%; 
		height: 100%; 
		display: flex; 
		justify-content: left;
		align-items: center;
	}
	.ico_22{
		width: 0.6rem;
		height: 0.6rem;
		position:absolute; 
		top:1.2rem; 
		left:0.95rem; 
		z-index:99;	
	}
	.b_list{
		font-size: 0.3rem; 
		color: #666; 
		line-height: 0.55rem;
	}
	.record li{
		float: left;
		width: 30%;
		display: flex; 
		justify-content: left;
		align-items: center;
	}
	.pic_see{
		width: 0.33rem; 
		height: 0.22rem; 
	}
	.pic_praise{
		width: 0.28rem; 
		height: 0.23rem; 
	}
	.hdzq_titil{
		color:#333; 
		font-size:0.36rem; 
		line-height: 0.6rem;
		margin-top: 0.15rem;
		overflow:hidden; 
		text-overflow:ellipsis; 
		white-space:nowrap;
	}
	.ts_label{
		font-size: 0.26rem; 
		background-color:#c0f4dc; 
		color: #666; 
		padding: 0.04rem 0.15rem; 
		margin: 0.1rem; 
		margin-left: 0; 
		border-radius: 0.05rem;
	}
	.ydjy_state{
		 background-color: #f60; 
		 color:#fff; 
		 font-size: 0.22rem; 
		 position:absolute; 
		 top:0.4rem; 
		 padding: 0.08rem 0.15rem;
		 z-index: 89;
		 border-radius: 0 0.2rem 0.2rem 0;    
	}
	.author{
		font-size: 0.3rem; 
		color: #666;
		line-height: 0.4rem;
	}
	.btn{
		margin-top: 0.5rem;
		width: 100%;
		height: 0.5rem;
		display: flex;
		flex-flow: row;
		justify-content: flex-start;
	}
	.btn a{
		width: 0.5rem;
		height: 0.5rem;
	}
	.btn a:nth-child(1){
		margin-left: 0.3rem;
		background-image: url('../../static/images/ico_22.png');
		background-size: 100% 100%;
	}
	.btn a:nth-child(2){
		margin-left: 1rem;
		/*width: 0.4rem;*/
		background-image: url('../../static/images/ico_24.png');
		background-size: 100% 100%;
	}
	.btn a:nth-child(3){
		margin-left: 1rem;
		/*width: 0.4rem;*/
		background-image: url('../../static/images/ico_11.png');
		background-size: 100% 100%;
	}
	.qx{
		position: fixed;
		bottom: 0px;
		left: 0px;
		z-index: 9999999999;
		width: 100%;
		padding: 0.1rem 0px;
		background-color: #40cc8b;
	}
	.qx a{
		display: block;
		width: 3rem;
		height: 0.8rem;
		border: 0.01rem solid #222222;
		background-color: #fff;
		font-size: 0.33rem;
		line-height: 0.8rem;
		text-align: center;
		margin: 0 auto;
		border-radius: 0.1rem;
		color: #111111;
	}
</style>


